.message_popup {
  position: fixed;
  right: 100px;
  bottom: 200px;

  width: 450px;
  aspect-ratio: 1/1;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 0 4px 2px rgba(0, 0, 0, 0.2), 0 0 12px 2px rgba(0, 0, 0, 0.2);

  display: grid;
  grid-template-rows: 15% 70% 15%;

  animation: show_popup 0.8s ease forwards;
}

.head_mess {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  border-bottom: solid 1px #e9e9e9;

  h6 {
    font-size: 1rem;
    margin-left: 5px;
  }

  button {
    border: none;
    background-color: #e4e7ea;
    padding: 5px 10px;
    color: #a2a2a2;
    font-style: italic;
  }
  button:hover {
    background-color: #acacac;
    color: #fff;
  }
}

.main_mess {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 10px;
  padding: 10px;

  p {
    font-size: 0.9rem;
  }
}

.bottom_mess {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  padding: 10px 20px;
  border-top: solid 1px #e9e9e9;
  background-color: #f9fafb;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;

  input {
    border: solid 1px transparent;
    background-color: #fff;
  }
}

.input_mess {
  width: 200px;
}

.paper_clip {
  transform: rotate(45deg);
  color: #a2a2a2;
  font-size: 1.2rem;
}

.felling {
  color: #a2a2a2;
}

.send_mess {
  color: #48b0f7;
}

@keyframes show_popup {
  0% {
    transform: translateY(100%) scale(0.3);
    opacity: 0;
  }

  40% {
    transform: rotate(-8deg) translateY(0) scale(1);
    opacity: 0.4;
  }
  60% {
    transform: rotate(6deg);
    opacity: 0.6;
  }
  100% {
    transform: rotate(0deg);
    opacity: 1;
  }
}
