.input_UI {
  width: 100%;
  position: relative;

  input {
    width: 100%;
    height: 100%;
    border: solid 1px #e0e4e8;
    padding: 8px 10px;
    font-size: 1rem;
  }

  input::placeholder {
    color: #a2a2a2;
  }

  input:hover {
    border: solid 1px #a1d0ff;
    z-index: 10;
  }

  input:focus {
    outline: none;
  }

  input:focus::placeholder {
    color: transparent;
  }

  i {
    position: absolute;
    left: 25px;
    bottom: 3px;
    z-index: 30;
    color: chocolate;
    font-size: 0.9rem;
  }

  h5 {
    font-weight: 400;
    margin-bottom: 8px;
  }
}
