body {
  margin: 0;
  font-family: "Segoe UI", -apple-system, BlinkMacSystemFont, "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* CSS chung các thẻ chữ trong project */
h2 {
  font-weight: 500;
  font-size: 2.3rem;
  font-style: italic;
  line-height: 2.6rem;
}

h3 {
  font-weight: 400;
  font-size: 1.4rem;
  font-style: italic;
}

h4 {
  font-weight: 500;
  font-size: 1rem;
  font-style: italic;
}

h5 {
  font-weight: 300;
  font-size: 1rem;
  font-style: italic;
  color: #a2a2a2;
}

p {
  font-size: 1rem;
  color: #a2a2a2;
  font-style: italic;
}
