.column_name {
  display: grid;
  grid-template-columns: 1fr 1.5fr 1fr 1fr 1fr 1fr;
  place-items: center;
  gap: 30px;
  text-align: center;
  background-color: #f8f9fa;
  padding: 20px 0;
}

.show_list_cart {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.nav_cart {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f8f9fa;
  padding: 15px;
  margin-top: 30px;

  > * {
    padding: 8px 10px;
    display: flex;
    align-items: center;
    gap: 8px;
  }

  > :hover {
    cursor: pointer;
    box-shadow: 0 0 2px #343434;
  }
}

.button_notice {
  border: solid 1px #000;
}

.empty_cart {
  text-align: center;
  padding: 20px;
  color: chocolate;
}
