.container_form {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("../../../public/images/banner1.jpg");
  background-size: 160%;
  background-repeat: repeat;
  background-position: center -50px;
  padding: 150px 0 150px 0;
  margin-bottom: -70px;
}

.card {
  width: 37%;
  padding: 60px 45px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgb(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.089);
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;

  h3 {
    color: #7f7f7f;
    font-size: 2rem;
    font-weight: 300;
    margin-bottom: 80px;
  }

  input {
    height: 70px;
    margin-top: -1px;
    padding: 25px;
    position: relative;
  }

  button {
    width: 100%;
    height: 60px;
    margin-top: 20px;
  }

  p {
    margin-top: 50px;
  }

  a {
    margin-left: 5px;
    text-decoration: none;
    color: rgb(53, 53, 255);
  }
}

.waning_form {
  position: absolute;
  left: 45px;
  top: 158px;
  color: chocolate;
  font-size: 0.9rem;
}
