.container {
  background-color: #111111;
  margin-top: 70px;
}

.footer_link {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 60px 10px;
  color: #f7f7f7;
  font-style: italic;

  li {
    display: flex;
    flex-direction: column;
  }

  h4 {
    font-weight: 300;
    font-size: 1.2rem;
    margin-bottom: 20px;
  }

  a {
    text-decoration: none;
    color: #393939;
    margin-bottom: 7px;
  }
}
