.category {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-top: 50px;
}

.title_category {
  text-align: center;
}

.list_category {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: 1fr auto;
  gap: 30px;

  > :nth-child(1) {
    grid-column: 1 / span 3;
    grid-row: 1;
    aspect-ratio: 7/4;
  }
  > :nth-child(2) {
    grid-column: 4 / span 3;
    grid-row: 1;
    aspect-ratio: 7/4;
  }
  > :nth-child(3) {
    grid-column: 1 / span 2;
    grid-row: 2;
  }
  > :nth-child(4) {
    grid-column: 3 / span 2;
    grid-row: 2;
  }
  > :nth-child(5) {
    grid-column: 5 / span 2;
    grid-row: 2;
  }
}
