.container {
  float: right;
  margin-top: 40px;
}

.button_page {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 6px;

  > * {
    width: 40px;
    aspect-ratio: 1/1;
    border: solid 1px #9e9e9e;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  b {
    font-size: 1.5rem;
    transform: translateY(-3px);
  }
}

.current_page {
  background-color: #0f100d;
  color: #fff;
  border: none;
  margin: 0 3px 0;
  font-style: italic;
}

.previous,
.current_page,
.next:hover {
  cursor: pointer;
}
