.image_product {
  width: 100%;
  aspect-ratio: 1/1;

  background-position: center;
  background-size: cover;
  position: relative;
}

.image_product::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.295);
  opacity: 0;
  transition: opacity 0.2s;
}

.image_product:hover::after {
  opacity: 1;
}
