.container {
  width: 900px;
  background-color: #fff;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  position: absolute;
  z-index: 100;
}

.popup_image {
  display: flex;
  align-items: center;
}

.popup_text {
  padding: 60px 30px;
  display: flex;
  flex-direction: column;
  gap: 7px;

  h3 {
    font-size: 1.8rem;
  }

  p {
    line-height: 1.4rem;
  }

  button {
    margin-top: 20px;
  }
}

.price_popup {
  font-size: 1.2rem;
}

.close_popup {
  position: absolute;
  top: 0;
  right: 0;
  padding: 15px;
}

.close_popup:hover {
  font-weight: bold;
  cursor: pointer;
}
