.nav {
  height: 55px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  padding: 0 15px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 50;
  background-color: rgb(255, 255, 255);

  button {
    background-color: transparent;
    border: none;
    padding: 5px;
    font-size: 1rem;
    font-style: italic;
  }

  button:hover {
    cursor: pointer;
  }

  i {
    color: #aaaaaa;
    margin-right: 3px;
  }

  li {
    display: flex;
    gap: 10px;
  }

  span {
    font-size: 0.9rem;
  }
}

.brand {
  font-size: 1.5rem;
}

.home_button {
  color: #deb867;
}
