.product_element {
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-align: center;
}

.animation {
  animation: show_product 0.7s ease forwards;
}

@keyframes show_product {
  from {
    transform: scale(0.3);
    opacity: 0;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
}
