.message_icon {
  width: 50px;
  aspect-ratio: 1/1;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: #000;
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 50;

  i {
    font-size: 35px;
    color: #fff;
    transform: translateY(-1px);
  }
}

.message_icon:hover {
  cursor: pointer;
}

.cover_icon {
  position: relative;
}

/* ----------------------------------------------------------------------- */

.message_animation {
  width: 50px;
  aspect-ratio: 1/1;
  border-radius: 50%;
  background-color: rgb(91, 91, 255);
  position: absolute;
  animation: get_attention 1s ease forwards 3;
}

@keyframes get_attention {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  90% {
    transform: scale(2);
    opacity: 0;
  }

  100% {
    transform: scale(1);
    opacity: 0;
  }
}
