.container {
  width: fit-content;
  text-wrap: nowrap;

  /* Ẩn spin button (tăng giảm) của thẻ input number */
  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"] {
    -webkit-appearance: none;
    -moz-appearance: textfield;
    appearance: none;
  }

  > * {
    border: none;
    outline: none;
    width: 20px;
    text-align: center;
    font-size: 1rem;
    background-color: transparent;
  }

  button:hover {
    cursor: pointer;
  }
}
