.title {
  height: 50px;
}

.brand {
  height: 40px;
  display: flex;
  align-items: center;
  color: #a2a2a2;
  background-color: #0f100d;
  padding: 0 10px 0 20px;
}

.product_lines {
  height: 40px;
  display: flex;
  align-items: center;
  background-color: #f6f9f6;
  padding: 0 10px 0 30px;
}

.nav {
  height: 40px;
  display: flex;
  align-items: center;
  color: #a2a2a2;
  padding: 0 10px 0 30px;
}

.title,
.brand,
.product_lines:hover {
  cursor: context-menu;
}

.nav:hover,
.active {
  color: rgb(212, 123, 60);
  font-weight: 400;
  cursor: pointer;
}
