.container {
  margin-top: 50px;

  h2 {
    font-size: 2.4rem;
  }

  h3 {
    font-size: 1.2rem;
  }

  b {
    font-style: italic;
    font-weight: 500;
  }

  p {
    line-height: 1.6;
    font-weight: 350;
  }
}

.main_info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
}

.image_detail {
  height: fit-content;
  display: grid;
  grid-template-columns: 18% 82%;
  gap: 15px;
}

.image_small {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
}

.info_detail {
  padding: 8px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.category {
  display: flex;
  align-items: center;
  gap: 8px;
}

.price_big {
  font-size: 1.3rem;
}

.add_to_cart {
  display: flex;
  width: 300px;
  text-wrap: nowrap;
}

.quantity_cover {
  width: 100%;
  border: solid 1px #e0e4e8;
  padding: 8px 20px;
  font-size: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 5px;
}

.description {
  margin-top: 150px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  p {
    white-space: pre-wrap;
  }
}
